<template>
    <v-row
            align="center"
            justify="center"
        >
        <v-col
            cols="6"
            xl="4"
            lg="6"
            sm="10"
            md="12"
        >
        <v-card class="elevation-12">
            <v-toolbar
            color="secondary"
            dark
            flat
            >
            <v-toolbar-title>Bridge Protocol Token Swap Request</v-toolbar-title>
            </v-toolbar>
            <v-card-text v-if="loading && !submitted">
                 <v-container text-center align-middle class="mt-8">
                    <v-progress-circular
                        indeterminate
                        color="secondary"
                    ></v-progress-circular>
                    <v-container>{{loadingLabel}}</v-container>
                </v-container>  
            </v-card-text>
            <v-card-text v-if="!loading && submitted">
                 <v-container text-center align-middle class="mt-8">
                    Transaction Submitted Successfully
                    <v-container>
                        Transaction Id: {{txid}}
                    </v-container>
                </v-container>  
            </v-card-text>
            <v-card-text v-if="!loading && !submitted">
                    <v-subheader class="pl-0 ml-0 caption">Addresses</v-subheader>
                    <v-row dense>
                        <v-col cols="1">
                            <v-img :src="`${publicPath}neo-logo.png`" height="20" contain></v-img>
                        </v-col>
                        <v-col cols="2">NEO</v-col>
                        <v-col cols="auto">{{accounts.neoAccount.address}}</v-col>
                    </v-row>
                    <v-row dense>
                        <v-col cols="1">
                            <v-img :src="`${publicPath}eth-logo.png`" height="20" contain></v-img>
                        </v-col>
                        <v-col cols="2">Ethereum</v-col>
                        <v-col cols="auto">{{accounts.ethAddress}}</v-col>
                    </v-row>
                    <v-subheader class="pl-0 ml-0 caption">Swap Amount</v-subheader>
                    <v-row dense>
                        <v-col cols="1">
                            <v-img :src="`${publicPath}bridge-token.png`" height="20" contain></v-img>
                        </v-col>
                        <v-col cols="2">
                            BRDG
                        </v-col>
                        <v-col cols="auto">{{brdgBalance}}</v-col>
                    </v-row>
                    <v-subheader class="pl-0 ml-0 caption">Transaction Priority</v-subheader>
                    <v-radio-group v-model="gas" :mandatory="true" row dense class="mt-0">
                        <v-radio label="Low (.001 GAS)" value=".001"></v-radio>
                        <v-radio label="Medium (.05 GAS)" value=".05"></v-radio>
                        <v-radio label="High (.1 GAS)" value=".1"></v-radio>
                    </v-radio-group>
            </v-card-text>
            <v-card-actions>
            <v-spacer />
            <v-btn color="secondary" @click="requestSwap" v-if="!loading && !submitted">Accept and Send Swap Request</v-btn>
            </v-card-actions>
        </v-card>
        </v-col>
    </v-row>
</template>

<script>
import NEO from '../scripts/neo.js';
export default {
    name: 'swap-request',
    props: ['accounts'],
    data: function () {
        return {
            publicPath: process.env.BASE_URL,
            gasBalance: 0,
            brdgBalance: 0,
            gas: ".1",
            loading: false,
            loadingLabel: "Please wait, getting balances",
            submitted: false,
            txid: null
        }
    },
    methods:{
        async requestSwap(){
            let gas = parseFloat(this.gas);
            if(gas > this.gasBalance){
                alert("Not enough GAS for transaction.  GAS Balance: " + this.gasBalance);
                return;
            }

            this.loading = true;
            this.loadingLabel = "Relaying transaction, please wait";
            try{
                let res = await NEO.sendRequest(this.accounts.neoAccount, this.brdgBalance, gas, this.accounts.ethAddress);
                if(!res || !res.txid)
                    throw new Error("Could not send transaction");

                this.txid = res.txid;
                this.loading = false;
                this.submitted = true;
            }
            catch(err){
                alert("Error sending transaction: " + err.message);
            }
        }
    },
    async created () {
        this.loading = true;
        let balances = await NEO.getBalances(this.accounts.neoAccount.address);
        this.gasBalance = balances.gas;
        this.brdgBalance = balances.brdg;
        this.loading = false;
    }
};
</script>