import fetch from 'node-fetch';
import { u, sc, wallet, api, tx } from '@cityofzion/neon-js'

let _brdgContractHash = "bac0d143a547dc66a1d6a2b7d66b06de42614971";

let neo = class {
    openWallet(wif){
        if(!wif)
            throw new Error("WIF not provided");

        return new wallet.Account(wif);
    }
    
    async sendRequest(account, amount, priorityFee, ethAddress)
    {
        return new Promise((resolve, reject) => {
            const scriptBuilder = new sc.ScriptBuilder();
            const addressScriptHash = u.reverseHex(wallet.getScriptHashFromAddress(account.address));
            const recipientScriptHash = u.reverseHex(wallet.getScriptHashFromAddress(account.address)); 
            const args = [
                addressScriptHash,
                recipientScriptHash,
                amount * 100000000
            ];
            scriptBuilder.emitAppCall(_brdgContractHash, 'transfer', args);
            const script = scriptBuilder.str;
            const config = {
                api: new api.neoscan.instance("MainNet"), 
                account: account,
                intents: [],
                script: script, 
                fees: priorityFee,
                gas: 0
              };
              
            api.fillSigningFunction(config)
                .then(c => { return api.fillUrl(c)})
                .then(c => { return api.fillBalance(c)})
                .then(c => { return api.createInvocationTx(c)})
                .then(c => { 
                    c.tx.addAttribute(
                        tx.TxAttrUsage.Script,
                        addressScriptHash
                    );
                    return c;
                })
                .then(c => { 
                    c.tx.addRemark(ethAddress + "-" + this._getRandom(3));
                    return c;
                })
                .then(c => { return api.signTx(c)})
                .then(api.sendTx)
                .then(c =>{
                    console.log(c.response);
                    resolve(c.response);
                })
                .catch(c => {
                    console.log(c);
                    reject(c);
                });
        });
    }

    async getBalances(address){
        let gas = 0;
        let brdg = 0;

        try{
            let res = await this._callNeoscan("get_balance", address);
            for (let i = 0; i < res.balance.length; i++) {
                if(res.balance[i].asset_symbol.toLowerCase() === "brdg")
                    brdg = res.balance[i].amount;
                else if(res.balance[i].asset_symbol.toLowerCase() === "gas")
                    gas = res.balance[i].amount;
            }
        }
        catch(err){
            console.log("Error getting balances: " + err.message);
        }

        return { gas, brdg };
    }

    _getRandom(len) {
        let str = u.sha256(u.str2hexstring(new Date().toISOString() + Math.random()));
        if (len)
            str = str.substring(0, len);
        return str;
    }

    async _callNeoscan(endpoint, param) 
    {
        let url = "https://neoscan.io/api/main_net/v1/" + endpoint + "/" + param;
        const response = await fetch(url, {method: 'GET'});

        if (response.ok) {
            let text = await response.text();
            if (text.length > 0)
                return JSON.parse(text);
            else
                return true;
        }
        else {
            var error = response.statusText;
            let text = await response.text();
            if (text) {
                var res = JSON.parse(text);
                if (res && res.message)
                    error = res.message;
            }
            else
                text = response.statusText;

            console.log(error);
            return {};
        }
    }
}

export default new neo();