<template>
    <v-row
        align="center"
        justify="center"
    >
        <v-col
            cols="6"
            xl="4"
            lg="6"
            sm="10"
            md="12"
        >
        <v-card>
            <v-card-text>
                <v-img :src="`${publicPath}logo.png`" height="200" contain></v-img>
                <v-form>
                    <v-text-field
                        v-model="wif"
                        label="NEO Private Key (WIF)"
                        name="privateKey"
                        prepend-icon="mdi-key"
                        type="text"
                    />
                    <v-text-field
                        v-model="ethAddress"
                        label="Ethereum Address"
                        name="ethAddress"
                        prepend-icon="mdi-lock"
                        type="text"
                    />
                </v-form>
            </v-card-text>
            <v-card-actions>
            <v-spacer />
            <v-btn color="secondary" @click="openWallet">Open Wallet and Continue</v-btn>
            </v-card-actions>
        </v-card>
        </v-col>
    </v-row>
</template>
<script>
import NEO from '../scripts/neo.js';
export default {
    name: 'open-wallet',
    data: function () {
        return {
            publicPath: process.env.BASE_URL,
            wif: null,
            ethAddress: null
        }
    },
    methods:{
        openWallet(){
            if(!this.wif){
                alert("NEO WIF is required");
                return;
            }
            if(!this.ethAddress){
                alert("Ethereum Address is required");
                return;
            }

            try
            {
                let account = NEO.openWallet(this.wif);
                this.$emit('accounts', { neoAccount: account, ethAddress: this.ethAddress });
            }
            catch(err){
                alert(err.message);
            }
        }
    },
    async created () {
       
    }
};
</script>