<template>
  <v-app>
    <v-content>
      <v-container
        class="fill-height"
        fluid
      >
        <open-wallet v-if="!accounts" @accounts="openWallet"></open-wallet>
        <swap-request v-if="accounts" :accounts="accounts"></swap-request>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import OpenWallet from './components/OpenWallet.vue';
import SwapRequest from './components/SwapRequest.vue';
export default {
  name: 'App',
  components: {
      OpenWallet,
      SwapRequest
  },
  data: () => ({
    accounts: null
  }),
  methods:{
    openWallet(accounts){
      this.accounts = accounts;
    }
  },
  created(){
    this.$vuetify.theme.dark = true;
  }
};
</script>
